  .get-access {
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    background-color:hsl(217, 28%, 15%);
    text-align: center;
    padding: 0.5rem;
    padding-bottom: 2rem;
    border-radius: 10px;
    p {
      width: 85%;
      margin: auto;
      padding-bottom: 2rem;
      
    }
    input {
      width: 300px;
      height: 50px;
      border-radius: 30px;
      margin-bottom : 1rem;
    };
  } 

  @media(min-width : 375px) {
    .get-access {
      width: 60%;
      p {
        width: 70%;
      }
      input {
        width: 400px;
      }
      button {
        width: 200px;
      }
    }

  }
